.i {
    display: flex;
    height:100vh;
}

.i-left {
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right {
    flex:1;
    position: relative;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-item {
    height: 50px;
    font-size:30px;
    font-weight: bold;
    color: purple;
    display: flex;
    align-items: center;
}

.i-title-wrapper {
    height: 100%;
    animation: move 15s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px); 
    }
}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    position: absolute;
}

.link {
    padding:50px;
}

